import React from "react";
// can't be
// import { React } from "react";
import ReactDOM from "react-dom";

// <!-- jsx (javaScript xml) expression
// jsx -- babel (compiler) --> js
// babeljs.io/repl
const element1 = <h1 id="hw1">Hello World #1</h1>;
// --- BABEL --- >
var element2 = React.createElement("h1", null, "Hello World #2");

console.log("element1: " + toJsonString(element1));
console.log("element2: " + toJsonString(element2));

// in '../public/index.html'
// <div id="root"></div>
ReactDOM.render(element1, document.getElementById("root"));
ReactDOM.render(element2, document.getElementById("root"));

ReactDOM.render(
    <h3 id="hw3">Hello World #3</h3>,
    document.getElementById("root")
);
console.log("element3 = " + toJsonString(<h3 id="hw3">Hello World #3</h3>));

var element4 = React.createElement("h4", null, "Hello World #4");
console.log("element4 = " + toJsonString(element4));
ReactDOM.render(element4, document.getElementById("root"));

const collection = React.createElement(
    "div",
    {
        id: "collectionOfObjects"
    },
    "This is div inside div with id='root'. This div contains element1, 2, 3 and 4. You can see json representation of those objects in console.",
    element1,
    element2,
    <h3 id="hw3">Hello World #3</h3>,
    element4
);
console.log("collection = " + toJsonString(collection));
ReactDOM.render(collection, document.getElementById("root"));

function toJsonString(object) {
    return JSON.stringify(object, null, 4);
}
